














import Vue from 'vue';

export default Vue.extend({
  data: () => ({
    email: '',
    password: '',
    loading: false,
  }),

  methods: {
    async signup() {
      this.loading = true;
      const result = await this.$store.dispatch('auth/signup', {
        email: this.email, password: this.password,
      });
      this.loading = false;
      if (result) {
        this.$emit('discard');
      }
    },

    toSignup() {
      this.$store.commit('stack/updateCards', [{
        type: 'login',
      }]);
      setTimeout(() => {
        this.$emit('discard');
      }, 50);
    },
  },
});
